<template>
	<div id="zj">
		<public-header title="蓝牙测试"></public-header>
		<div class="content">
			<van-cell-group v-if="peripherals.length > 0">
				<van-cell :title="item.name" value="点击连接" @click="onConnect(index)" v-for="(item, index) in peripherals" :key="index" />
			</van-cell-group>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import cloudApp from "@/config/cloudapp.js"
	export default {
		data() {
			return {
				peripherals: [],
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onConnect(index) {
				var that = this;
				alert(JSON.stringify(that.peripherals[index]));
				alert(that.peripherals[index].uuid)
				that.ble.connect({
					peripheralUUID: that.peripherals[index].uuid,
				}, function(ret, err) {
					alert(JSON.stringify(ret));
					if (ret.status) {
						alert("连接成功！");
					} else {
						alert(JSON.stringify(err));
					}
				});
			}
		},
		mounted() {
			var that = this;
			//let ble = that.util.isHasModule('ble');
			cloudApp.initBle();
			that.ble = cloudApp.ble;
			that.ble.scan({serviceUUIDs: []}, function(ret) {
				//alert(ret.status)
				if (ret.status) {
					//alert('开始扫描');
					that.ble.getPeripheral(function(ret) {
						if(ret) {
							that.peripherals = [...ret.peripherals];
							alert(JSON.stringify(that.peripherals.length));
						}
					});
				}
			})

		}
	}
</script>

<style scoped>

</style>


